@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

label{padding:5px}#date{border:1px solid gray;border:1px solid #ced4da;border-radius:3px;padding:3px}
body{background:#171e24 !important}.App{text-align:right;padding:15px;color:#fff;background:#171e24;font-family:"Roboto",sans-serif}.App-logo{height:40vmin;pointer-events:none}@media(prefers-reduced-motion: no-preference){.App-logo{-webkit-animation:App-logo-spin infinite 20s linear;animation:App-logo-spin infinite 20s linear}}.App-header{background-color:#282c34;min-height:100vh;display:flex;flex-direction:column;align-items:center;justify-content:center;font-size:calc(10px + 2vmin);color:#fff}.App-link{color:#61dafb}@-webkit-keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}@keyframes App-logo-spin{from{transform:rotate(0deg)}to{transform:rotate(360deg)}}table tr,input{cursor:pointer}.fc-view,.fc-unthemed .fc-list-empty{background-color:#353b47 !important}.fc-widget-header{background:#59596b !important}.fc-view-container{border:none}.fc-view-container div{border:none}
